import React, { useContext, useEffect, useState } from "react";
import "./App.css";
import "semantic-ui-css/semantic.min.css";
import Logo from "./images/logo2.png";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import {
  Image,
  Container,
  Header,
  Card,
  Label,
  Popup,
  Rating,
  Radio,
  Dimmer,
  Loader,
  Button,
  Divider,
  Segment,
  Dropdown,
  Icon,
  Input,
  Modal,
  Grid,
  Item,
  Checkbox,
  Menu,
} from "semantic-ui-react";
import Home from "./Home";
import Productos from "./Productos";
import Cart from "./Cart";
import { Carrito } from "./Contexto";

export default function APP() {
  const [user, setUser] = useState("");
  const [activeMenu, setActiveMenu] = useState("home");
  const { cantidades } = useContext(Carrito);

  function SelectMenu(e, { name }) {
    setActiveMenu(name);
  }
  return (
    <Router basename={"/"}>
      <Container style={{ padding: "1%" }}>
        <Image src={Logo} size="tiny" className="bajar" />

        <Segment style={{ height: "75vh" }}>
          <Switch>
            <Route
              path="/cart"
              render={(props) => <Cart {...props} user={user} />}
            />
            <Route
              path="/productos"
              render={(props) => <Productos {...props} user={user} />}
            />
            <Route
              path="/"
              render={(props) => <Home {...props} user={user} />}
            />
          </Switch>
        </Segment>
        <Menu icon="labeled" fluid widths={4} color="red">
          <Menu.Item
            name="home"
            onClick={SelectMenu}
            active={activeMenu === "home"}
            as={Link}
            to="/"
          >
            <Icon name="home" />
            Inicio
          </Menu.Item>

          <Menu.Item
            name="productos"
            onClick={SelectMenu}
            active={activeMenu === "productos"}
            as={Link}
            to="/productos"
          >
            <Icon name="food" />
            Productos
          </Menu.Item>

          <Menu.Item
            name="user"
            onClick={SelectMenu}
            active={activeMenu === "user"}
            as={Link}
            to="/"
          >
            <Icon name="user" />
            Clientes
          </Menu.Item>

          <Menu.Item
            name="shop"
            onClick={SelectMenu}
            active={activeMenu === "shop"}
            as={Link}
            to="/cart"
          >
            <Icon name="shop" />
            <Label circular color="red" floating>
              {cantidades}
            </Label>
            Carrito
          </Menu.Item>
        </Menu>
      </Container>
    </Router>
  );
}
