import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import bocadillo from "./images/bocadillo.png";
import chocolate from "./images/chocolate.png";
import coca from "./images/coca.png";
import cena from "./images/cena.png";
import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Statistic,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.Disponibles;
  const fechaB = b.Disponibles;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

export default function Home(user) {
  const {
    ["cantidades"]: [cantidades, setCantidades],
  } = useContext(Carrito);
  const categorias = [
    { name: "Snacks", src: bocadillo, categ: "Snacks" },
    { name: "Dulces", src: chocolate, categ: "Dulces" },
    { name: "Bebidas", src: coca, categ: "Bebida" },
    { name: "Otros", src: cena, categ: "Otros" },
  ];
  const topProducts = [
    {
      name: "KaChitos Clasicos",
      src:
        "https://msflordeloto.com/wp-content/uploads/2020/05/kachitos-de-117-g-.jpg",
      cat: "Snacks",
      price: 0.45,
      id: "fde03769",
    },
    {
      name: "Coca Cola Zero 12oz Lata",
      src:
        "https://cdn.shopify.com/s/files/1/2538/5286/products/651820_1500x1500.jpg?v=1588778658",
      cat: "Bebida",
      price: 0.9,
      id: "j376FUT4",
    },
    {
      name: "Oreo",
      src:
        "http://sipanfood.com/wp-content/uploads/2020/04/galleta-oreo-768x768.jpg",
      cat: "Snacks",
      price: 0.45,
      id: "hEGF6Jsp",
    },
  ];
  const [data, setData] = useState();
  const [categoria, setCat] = useState("Snacks");
  const [open, setOpen] = useState(false); //Top Product
  const [activeItem, setActiveItem] = useState("");
  const [loading, setLoading] = useState(false);
  const [buscar, setBuscar] = useState("");
  const [carro, setCarro] = useState(0);
  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    setLoading(false);
  };
  const {
    ["articulos"]: [articulos, setArticulos],
  } = useContext(Carrito);
  const agregar = (e) => {
    setOpen(false);
    let articulo = activeItem;
    console.log(articulo);
    let i;
    let existe = false;
    for (i = 0; i < articulos.length; i++) {
      if (articulos[i].name === articulo.name) {
        existe = true;
        articulos[i].cant = articulos[i].cant + 1;
        articulos[i].amt = articulos[i].cant * articulos[i].price;
        break;
      }
    }
    if (!existe) {
      articulos.push(articulo);
    }
    console.log(articulos);
    for (i = 0; i < articulos.length; i++) {
      //let result = articulos.reduce((r, d) => r + d.amt, 0);
      //setTtlamt(result);
    }
    setCantidades(articulos.length);
  };

  useEffect(() => {
    if (!data) {
      sacardata();
    }
  }, [data]);

  return (
    <Container>
      <Dimmer active={loading} page>
        <Loader>Loading</Loader>
      </Dimmer>
      <Modal
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}
        open={open}
        size="small"
      >
        <Modal.Header>{activeItem.name}</Modal.Header>
        <Modal.Content>
          <Image size="small" src={activeItem.src}></Image>
          <Modal.Description>
            <p>
              {activeItem.price
                ? currencyFormat(activeItem.price)
                : activeItem.price}
            </p>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button
            //disabled={activeItem.disponible <= 0}
            color="green"
            onClick={agregar}
          >
            {activeItem.disponible <= 0 ? "Out of Stock" : "Agregar"}
          </Button>
          <Button
            color="red"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancelar
          </Button>
        </Modal.Actions>
      </Modal>
      <Segment style={{ display: "flex", height: "10vh" }}>
        {categorias.map((cat) => (
          <p style={{ margin: "auto" }}>
            <Popup
              content={cat.name}
              key={cat.name}
              trigger={
                <Image
                  src={cat.src}
                  size="mini"
                  onClick={() => {
                    setCat(cat.name);
                  }}
                />
              }
            />
            <span style={{ color: cat.name === categoria ? "red" : "black" }}>
              {cat.name}
            </span>
          </p>
        ))}
      </Segment>
      <Header>Lo Mas Vendido</Header>
      <Segment
        className="pegados"
        style={{ padding: "2%", height: "15vh", marginLeft: "1%" }}
      >
        {topProducts.map((item) => (
          <div
            name={item.name}
            className="bordecito"
            style={{
              margin: "auto",
              alignItems: "center",
              alignContent: "center",
            }}
          >
            {
              <img
                onClick={() => {
                  open ? setOpen(false) : setOpen(true);
                  setActiveItem((prevObject) => ({
                    ...prevObject,
                    name: item.name,
                    price: item.price,
                    src: item.src,
                    id: item.id,
                    cant: 1,
                    amt: item.price,
                  }));
                }}
                src={item.src}
                alt=""
                width="75"
                height="75"
              ></img>
            }
          </div>
        ))}
      </Segment>
      <Input
        icon="search"
        placeholder="Buscar..."
        size="small"
        onChange={(e, { value }) => {
          setBuscar(value);
        }}
      />
      <Segment style={{ height: "25vh", overflowY: "scroll" }}>
        {data &&
          data
            .sort(compare)
            .filter((item) => {
              return (
                item.Product_Name.toLowerCase().includes(
                  buscar ? buscar.toLowerCase() : buscar
                ) && item.Categoria === categoria
              );
            })
            .map((item) => (
              <div
                style={{
                  display: "inline-block",
                  width: "28vw",
                  height: "30vh",
                  margin: "auto",
                }}
                onClick={() => {
                  open ? setOpen(false) : setOpen(true);
                  setActiveItem((prevObject) => ({
                    ...prevObject,
                    name: item.Product_Name,
                    price: item.Price,
                    src: item.Image,
                    id: item.Product_Id,
                    cant: 1,
                    amt: item.Price,
                    disponible: item.Disponibles,
                  }));
                }}
              >
                <p>{item.Product_Name}</p>
                <img src={item.Image} alt="" width="75" height="75"></img>
                <p>{currencyFormat(item.Price)}</p>
                <p>
                  {item.Disponibles > 0 ? item.Disponibles : "Out of Stock"}
                </p>
              </div>
            ))}
      </Segment>
    </Container>
  );
}
