import React, { useContext, useState } from "react";

export const Carrito = React.createContext(null);

export default ({ children }) => {
  const [cantidades, setCantidades] = useState(0);
  const [articulos, setArticulos] = useState([]);
  const carrito = {
    cantidades: [cantidades, setCantidades],
    articulos: [articulos, setArticulos],
  };

  return <Carrito.Provider value={carrito}>{children}</Carrito.Provider>;
};
